<template>
  <div id="content">
    <div class="block" v-for="item in gameList" :key="item.name" :style="{backgroundColor: item.color}" @click="goto(item.path)">
      <div class="innerBlock">
        <div class="name">{{item.name}}</div>
        <div class="desc">{{item.desc}}</div>
      </div>
    </div>
  </div>
</template>

<style scoped lang="scss">
  #content {
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-flow: row wrap;
    align-items: stretch;
    align-content: stretch;
    .block{
      min-width: 25%;
      flex: 1;
      cursor: pointer;
      .innerBlock{
        width: 100%;
        height: 100%;
        padding: 0 20px;
        box-sizing: border-box;
        background-color: rgba(0,0,0,0.5);
        transition: background-color 300ms;
        display: flex;
        flex-flow: column nowrap;
        justify-content: center;
        align-items: center;
        .name{
          color: rgba(255,255,255,.5);
          transition: color 500ms;
          font-size: 20px;
          font-weight: bold;
        }
        .desc{
          margin-top: 10px;
          color: rgba(255,255,255,.25);
          transition: color 500ms;
          font-size: 12px;
        }
      }
      .innerBlock:hover{
        background-color: rgba(0,0,0,0);
        .name{
          color: rgba(0,0,0,.75);
        }
        .desc{
          color: rgba(0,0,0,.5);
        }
      }
    }
  }
</style>

<script>
export default {
  name: 'gameMenu',
  data () {
    return {
      gameList: [
        {
          name: "",
          path: "/minesweeper",
          desc: "",
          color: "#b0b0b0",
        },
        {
          name: "",
          path: "/snake",
          desc: "WASD",
          color: "#707070",
        },
        {
          name: "",
          path: "/color",
          desc: "",
          color: "#ff5e52",
        },
        {
          name: "",
          path: "/link",
          desc: "",
          color: "#ffef6e",
        },
        {
          name: "",
          path: "/jump",
          desc: "WASD",
          color: "#6cd7ff",
        },
        {
          name: "Picross",
          path: "/picross",
          desc: "Picross",
          color: "#5affb6",
        },
        {
          name: "",
          path: "/digital",
          desc: " - Bulls and Cows",
          color: "#e19bff",
        },
        {
          name: "",
          path: "/wolf",
          desc: "kill",
          color: "#ffbc64",
        },
      ]
    }
  },
  created(){
    //
    document.oncontextmenu = function(){
      event.returnValue = false;
    };
    //
    document.onselectstart = function(){
      event.returnValue = false;
    };
  },
  methods: {
    goto(url){
      this.$router.push(url);
    }
  }
}
</script>



